import { Pipe, PipeTransform } from '@angular/core';


@Pipe( { name: 'ui' } )
export class UiPipe implements PipeTransform {
    readonly RE_COLORCODE_FULL = /\[[0-9a-f]{8}\][^\[]*\[\/[0-9a-f]{8}\]/g;
    readonly RE_COLORCODE = /\[\/?[0-9a-f]{8}\]/g;
    readonly RE_SPEC_CHARS = /[\[\]\/]/g;
    readonly HTML_START = '<font color="%">';
    readonly HTML_END = '</font>';

    transform( value: any ): string {
        let returnValue: string = '';

        if ( value.length == 2 )
            returnValue = value[1]; //perfect

        if ( value.constructor.name === Object.name )
            returnValue = JSON.stringify( value );

        return this.transformCommandTags( returnValue );
    }

    private transformCommandTags( value: string ): string {
        let returnValue: string = value;

        return this.transformColorCode( returnValue );
    }


    private transformColorCode( value: string ): string {
        let returnValue: string = value;

        let colorCodeFull = this.RE_COLORCODE_FULL.exec( value );
        while ( colorCodeFull != null ) {
            
            let colorCodeMatch = this.RE_COLORCODE.exec(colorCodeFull[0]);
            let colorCode = colorCodeMatch[0].replace( this.RE_SPEC_CHARS, '' );
            let content = colorCodeFull[0].replace(this.RE_COLORCODE, '');
            
            let newString = this.HTML_START.replace('%', colorCode);
            newString += content;
            newString += this.HTML_END;
            
            returnValue = returnValue.replace(colorCodeFull[0], newString);
            
//            console.log( 'colorCode:', colorCode );
//            console.log( 'content:', content );
            colorCodeFull = this.RE_COLORCODE_FULL.exec( value );
        }
        return returnValue;
    }

}